.faq__list {
  width: 100%;
  display: block;
}

.faq__item {
  padding: 30px 0;
  border-bottom: 1px solid #102654;
  position: relative;
}
.faq__item:first-child {
  border-top: 1px solid #102654;
}

.faq__text-medium {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 29px;
  padding-bottom: 15px;
}

.faq__text {
  width: 80%;
  max-height: 100%;
  max-width: 660px;
  font-size: 18px;
  font-weight: 300;
  line-height: 25px;
}

.faq__text--active {
  display: block;
}

.faq__more {
  width: 42px;
  height: 42px;
  position: absolute;
  top: 20px;
  right: 0;
}

.faq__plus {
  display: block;
  width: 42px;
  height: 42px;
  border-radius: 100%;
  background-color: inherit;
  border-color: #102654;
  position: relative;
  cursor: pointer;
}
.faq__plus::after, .faq__plus::before {
  content: "";
  width: 18px;
  height: 1px;
  position: absolute;
  background-color: #102654;
  transform: translate(-50%, -50%);
}
.faq__plus::before {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.faq__plus::after {
  transform: translate(-50%, -50%) rotate(90deg);
}
.faq__plus:hover, .faq__plus:focus-visible {
  background-color: #102654;
}
.faq__plus:hover::after, .faq__plus:hover::before, .faq__plus:focus-visible::after, .faq__plus:focus-visible::before {
  background-color: #ffffff;
}

.faq__item.is-active .faq__plus {
  background-color: #1c3374;
}
.faq__item.is-active .faq__plus::before {
  background-color: #ffffff;
}
.faq__item.is-active .faq__plus::after {
  display: none;
}

